import React, { Component, useState, useEffect, useRef} from 'react';
import './App.css';

const { kakao } = window;

function Kakaomap() { 
    const [inputText, setInputText] = useState("");
    const [searchPlace, setsearchPlace] = useState("ì¥ì†Œ");  
     // ì»´í¬ë„ŒíŠ¸ê°€ ë¡œë“œë˜ëŠ” ìˆœê°„ ì¹´ì¹´ì˜¤í‚¤ì›Œë“œê²€ìƒ‰ ajax.getìš”ì²­ ì´ ì‹¤í–‰ë˜ê¸° ê¸°ë³¸ê°’ "" ì´ê±° ë•Œë¬¸ì— ìš”ì²­urlë¶€ë¶„ query=& ìœ¼ë¡œ queryê°’ì´ ë¹ˆê°’ìœ¼ë¡œ ìš”ì²­ëœë‹¤. ë”°ë¼ì„œ 400 ì—ëŸ¬ ë©”ì„¸ì§€ê°€ ëœ¬ë‹¤.
     // ì˜¤ë¥˜ë¥¼ ë§‰ê³ ì ê¸°ë³¸ê°’ì„ ì•„ë¬´ê±°ë‚˜ ì§€ì •í›„ , ë˜í•˜ë‚˜ì˜ ìƒíƒœê°’ì„ ë§Œë“¤ì–´ ì¥ì†Œê²€ìƒ‰ì‹œë§Œ ckStatus = trueë¡œ ë³€ê²½ë˜ì–´ ì¥ì†Œê²€ìƒ‰getìš”ì²­ì´ ì‹¤í–‰ëœë‹¤. 
    const [showplace, setshowplace] = useState([]);
    let [ckStatus, setckStatus] = useState(false);
    let location = useRef();
    let location2 = useRef();    

    
    useEffect(() => {
         let infowindow = new kakao.maps.InfoWindow({zIndex:1});
         const mapcontainer = document.getElementById('kakaomap');
         const options = {
             center: new kakao.maps.LatLng(33.450701, 126.570667),
             level: 3
         };
         const map = new kakao.maps.Map(mapcontainer, options);  // ì§€ë„ë¥¼ ìƒì„±í•©ë‹ˆë‹¤

         // HTML5ì˜ geolocationìœ¼ë¡œ ì‚¬ìš©í•  ìˆ˜ ìˆëŠ”ì§€ í™•ì¸í•©ë‹ˆë‹¤ 
         if (navigator.geolocation) {       
             // GeoLocationì„ ì´ìš©í•´ì„œ ì ‘ì† ìœ„ì¹˜ë¥¼ ì–»ì–´ì˜µë‹ˆë‹¤
             navigator.geolocation.getCurrentPosition(function(position) {                
                const lat = position.coords.latitude, // ìœ„ë„
                      lon = position.coords.longitude; // ê²½ë„     
                    //   console.log(lat);                
                const locPosition = new kakao.maps.LatLng(lat, lon), // ë§ˆì»¤ê°€ í‘œì‹œë  ìœ„ì¹˜ë¥¼ geolocationìœ¼ë¡œ ì–»ì–´ì˜¨ ì¢Œí‘œë¡œ ìƒì„±í•©ë‹ˆë‹¤
                      message = '<div id="me" style="padding:3px;">me</div>'; // ì¸í¬ìœˆë„ìš°ì— í‘œì‹œë  ë‚´ìš©ì…ë‹ˆë‹¤
                
                // ë§ˆì»¤ì™€ ì¸í¬ìœˆë„ìš°ë¥¼ í‘œì‹œí•©ë‹ˆë‹¤
                currDisplayMarker(locPosition, message);                    
             });
            
         } else { // HTML5ì˜ GeoLocationì„ ì‚¬ìš©í•  ìˆ˜ ì—†ì„ë•Œ ë§ˆì»¤ í‘œì‹œ ìœ„ì¹˜ì™€ ì¸í¬ìœˆë„ìš° ë‚´ìš©ì„ ì„¤ì •í•©ë‹ˆë‹¤            
             const locPosition = new kakao.maps.LatLng(33.450701, 126.570667),    
                   message = 'geolocationì„ ì‚¬ìš©í• ìˆ˜ ì—†ì–´ìš”..'
                
                   currDisplayMarker(locPosition, message);
         }

         // ì§€ë„ì— ë§ˆì»¤ì™€ ì¸í¬ìœˆë„ìš°ë¥¼ í‘œì‹œí•˜ëŠ” í•¨ìˆ˜ì…ë‹ˆë‹¤
         function currDisplayMarker(locPosition, message) {
             // ë§ˆì»¤ë¥¼ ìƒì„±í•©ë‹ˆë‹¤
             const marker = new kakao.maps.Marker({  
                   map: map, 
                   position: locPosition
             });             
             const iwContent = message, // ì¸í¬ìœˆë„ìš°ì— í‘œì‹œí•  ë‚´ìš©
                   iwRemoveable = true;

             // ì¸í¬ìœˆë„ìš°ë¥¼ ìƒì„±í•©ë‹ˆë‹¤
             let infowindow = new kakao.maps.InfoWindow({
                 content : iwContent,
                 // removable : iwRemoveable
             });
            
             // ì¸í¬ìœˆë„ìš°ë¥¼ ë§ˆì»¤ìœ„ì— í‘œì‹œí•©ë‹ˆë‹¤ 
             infowindow.open(map, marker);
            
             // ì§€ë„ ì¤‘ì‹¬ì¢Œí‘œë¥¼ ì ‘ì†ìœ„ì¹˜ë¡œ ë³€ê²½í•©ë‹ˆë‹¤
             map.setCenter(locPosition);      
         }    

         // ì¥ì†Œ ê²€ìƒ‰ ê°ì²´ë¥¼ ìƒì„±
         const ps = new kakao.maps.services.Places();


         if(ckStatus){  // ë˜í•˜ë‚˜ì˜ ìƒíƒœê°’ì„ ë§Œë“¤ì–´ ì¥ì†Œê²€ìƒ‰ì‹œë§Œ ckStatus = trueë¡œ ë³€ê²½ë˜ì–´ ì¥ì†Œê²€ìƒ‰getìš”ì²­ì´ ì‹¤í–‰ëœë‹¤. 
             // í‚¤ì›Œë“œë¡œ ì¥ì†Œë¥¼ ê²€ìƒ‰
             ps.keywordSearch(searchPlace, placesSearchCB); 

             function placesSearchCB (data, status, pagination) {
                if (status === kakao.maps.services.Status.OK) {
                    let bounds = new kakao.maps.LatLngBounds();
                    for (let i=0; i<data.length; i++) {
                        displayMarker(data[i]);     
                        bounds.extend(new kakao.maps.LatLng(data[i].y, data[i].x));
                    }       
                    map.setBounds(bounds);
                     // í˜ì´ì§€ ëª©ë¡ ë³´ì—¬ì£¼ëŠ” displayPagination() ì¶”ê°€
                      displayPagination(pagination);
                  //    console.log(data);                
                      setshowplace(data);
                   //  console.log(showplace);
                } 
              }
    
            // ê²€ìƒ‰ê²°ê³¼ ëª©ë¡ í•˜ë‹¨ì— í˜ì´ì§€ ë²ˆí˜¸ í‘œì‹œ
            function displayPagination(pagination) {
                var paginationEl = document.getElementById('pagination'),
                    fragment = document.createDocumentFragment(),
                    i
        
                // ê¸°ì¡´ì— ì¶”ê°€ëœ í˜ì´ì§€ ë²ˆí˜¸ ì‚­ì œ
                while (paginationEl.hasChildNodes()) {
                paginationEl.removeChild(paginationEl.lastChild)
                }
        
                for (i = 1; i <= pagination.last; i++) {
                    var el = document.createElement('div')
                    // el.href = '#'
                    el.innerHTML = i
            
                    if (i === pagination.current) {
                        el.className = 'on'
                    } else {
                        el.onclick = (function (i) {
                            let maptop = location.current.offsetTop;
                         //   console.log(maptop);
                            window.scrollTo({top:maptop + 350, behavior:'smooth'});
                            return function () {
                                pagination.gotoPage(i)
                            }
                        })(i)
                    }    
                    fragment.appendChild(el)
                }
                paginationEl.appendChild(fragment)
            }  
    
            // ì§€ë„ì— ë§ˆì»¤ë¥¼ í‘œì‹œí•˜ëŠ” í•¨ìˆ˜   
            function displayMarker(place) {
             //   console.log(place);
                let marker = new kakao.maps.Marker({
                    map: map,
                    position: new kakao.maps.LatLng(place.y, place.x) 
                });
                kakao.maps.event.addListener(marker, 'click', function () {
                    infowindow.setContent('<div id="view_p" style="padding:3px;font-size:11px;">' + place.place_name + '</div>')
                    infowindow.open(map, marker)
                  });
             } 
             
             function setDraggable(flase) {
                // ë§ˆìš°ìŠ¤ ë“œë˜ê·¸ë¡œ ì§€ë„ ì´ë™ ê°€ëŠ¥ì—¬ë¶€ë¥¼ ì„¤ì •í•©ë‹ˆë‹¤
                map.setDraggable(flase);    
            }

         }       

         return () => { setckStatus(false); }

     }, [searchPlace] );    
 

     useEffect( () => { 
        location.current.addEventListener('click', () => { 
            location.current.classList.add('km');
            location2.current.classList.add('km2');
        } );
       }, []);
    
     return(
          <>
            <h3 className="youph kamtitle">ì¥ì†Œë¥¼ ê²€ìƒ‰í•´ ë³´ì</h3>  
            <div id="jinheemap" ref={location} >
                <div id="kakaomap"> 
                </div>
                <form onSubmit={ (e) => { 
                        e.preventDefault();
                        setsearchPlace(inputText);
                        setckStatus(true);
                        setInputText("");
                    } } >
                    <input className="search" placeholder="Search Place" 
                        onChange={ (e) => { setInputText(e.target.value); } } value={inputText}/> 
                    <button className="mapbt" type="submit">ê²€ìƒ‰</button>
                </form>
                <div id="showplace" ref={location2} >
                {  
                   showplace.map( (item, i) => { 
                       return(
                          <div className="kamapbox" key={i} >
                          <h5>{item.place_name} <span>{item.category_group_name}</span></h5>
                          <h6>{item.road_address_name}</h6>
                          <h6>{item.phone} </h6>       
                          </div>   
                       );                
                   } )                
                }  
                <div id="pagination"></div>
                </div>
            </div>
          </>    
     );
 }

 export default Kakaomap;